import React ,{ useState } from "react";
import { unstable_createPortal } from "react-dom/cjs/react-dom.development";

const App=()=>{

  const [cval , uval] = useState();

  const [carr , uarr] = useState([]);

  const inputItem=(event)=>{
    uval(event.target.value);
  }

  const click1=()=>{
    
    uarr((preVal)=>{
      return[
        ...preVal,
        cval
      ];
    })
    uval("");
  }

  const click2=(id)=>{
    uarr((oldItem)=>{
      return oldItem.filter((arrElement , index )=>{
        return index !==id;
      })
    })
  };

  return(
    <div className="back">
      <div className="card">
        <h1> TODO LIST</h1>
        <div className="top">
          <input type="text" placeHolder="enter an item name" autoComplete="off" onChange={inputItem} value={cval}/>
          <button className="button1" onClick={click1}>+</button>
        </div> 
        <ul>
            {carr.map((value,index)=>{
              return (
                <>
                <li>
                 <button className="button2" onClick={()=>{click2(index)}}>x</button>
                 <h2>{value}</h2>
                </li>
                </>
                );
            })}
        </ul>
      </div>
    </div>
  );
}

export default App ;